body {
  --blue-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  display: flex;
}

.nav {
  margin: 0 50px 0 0;
  display: flex;
}

.ul {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.nav_ul__li {
  text-transform: uppercase;
  align-items: center;
  margin-right: 20px;
  display: flex;
}

li:last-child {
  margin-right: 0;
}

.logo-img {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.img-link {
  align-items: center;
  display: flex;
}

.anchor {
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

a:link, a:visited {
  color: #000;
}

a:hover {
  color: var(--blue-color);
}

a:active {
  color: green;
}

a.nav__link.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
